html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
  margin-bottom: -5px;
}

body {
  font-family: "Space Grotesk", sans-serif;
  font-size: 18px;
  background-color: hsl(0deg, 0%, 100%);
  height: 100vh;
  position: relative;
}

.container {
  width: min(90%, 120rem);
  margin-inline: auto;
}

.error {
  outline: 1px solid hsl(0deg, 100%, 66%);
}

.error-msg {
  color: hsl(0deg, 100%, 66%);
  font-size: 1rem;
}

.cards {
  position: relative;
}
@media (min-width: 1440px) {
  .cards {
    height: 100%;
  }
}
.cards__card img {
  width: 100%;
  height: 100%;
}
@media (min-width: 1440px) {
  .cards__card {
    height: 100%;
  }
  .cards__card:nth-child(1) {
    width: 35%;
  }
}
.cards__card:nth-child(2), .cards__card:nth-child(3) {
  position: absolute;
  width: 80%;
  border-radius: 1rem;
}
@media (min-width: 1440px) {
  .cards__card:nth-child(2), .cards__card:nth-child(3) {
    width: 30%;
    height: 30%;
  }
}
.cards__card:nth-child(2) {
  top: 11%;
  right: 5%;
}
@media (min-width: 1440px) {
  .cards__card:nth-child(2) {
    left: 20%;
    bottom: 15%;
    top: auto;
    -webkit-box-shadow: 36px -48px 66px -50px rgba(0, 0, 0, 0.44);
    -moz-box-shadow: 36px -48px 66px -50px rgba(0, 0, 0, 0.44);
    box-shadow: 36px -48px 66px -50px rgba(0, 0, 0, 0.44);
  }
}
.cards__card:nth-child(3) {
  top: 49%;
  left: 5%;
  -webkit-box-shadow: 20px 55px 94px -44px rgba(0, 0, 0, 0.44);
  -moz-box-shadow: 20px 55px 94px -44px rgba(0, 0, 0, 0.44);
  box-shadow: 20px 55px 94px -44px rgba(0, 0, 0, 0.44);
}
@media (min-width: 1440px) {
  .cards__card:nth-child(3) {
    top: 20%;
    left: 15%;
    -webkit-box-shadow: 36px 48px 89px -50px rgba(0, 0, 0, 0.44);
    -moz-box-shadow: 36px 48px 89px -50px rgba(0, 0, 0, 0.44);
    box-shadow: 36px 48px 89px -50px rgba(0, 0, 0, 0.44);
  }
}
.cards__logo img {
  width: 7rem;
  margin-top: 2rem;
  margin-left: 2rem;
}
.cards__card--credit {
  color: hsl(0deg, 0%, 100%);
  background-image: url("../img/bg-card-front.png");
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 1rem;
}
.cards__content {
  margin: 4rem 2rem 2rem 2rem;
}
@media (min-width: 1440px) {
  .cards__content {
    margin-top: 8rem;
  }
}
.cards__num {
  display: flex;
  justify-content: space-between;
  letter-spacing: 2px;
  font-size: 2rem;
}
@media (min-width: 1440px) {
  .cards__num {
    width: 80%;
    justify-content: unset;
    gap: 2rem;
    font-size: 2.5rem;
  }
}
.cards__box {
  display: flex;
  justify-content: space-between;
  margin-block: 2rem;
  letter-spacing: 1px;
  font-size: 1.5rem;
}
@media (min-width: 1440px) {
  .cards__box {
    margin-top: 3.5rem;
  }
}
.cards__name {
  text-transform: uppercase;
}

.info {
  margin-top: 10rem;
}
.info.none {
  display: none;
}
@media (min-width: 1440px) {
  .info {
    position: absolute;
    top: 50%;
    width: 25%;
    left: 50%;
    right: 2rem;
    transform: translateY(-80%);
  }
}
.info__space {
  color: hsl(278deg, 68%, 11%);
}
.info__space label {
  display: block;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1.2rem;
  margin-block: 1rem;
}
.info__space input {
  margin-bottom: 1rem;
  font-family: "Space Grotesk", sans-serif;
  font-size: 1.5rem;
  letter-spacing: 1px;
  display: block;
  width: 100%;
  padding: 1rem;
  border-radius: 1rem;
  border: 1px solid hsl(270deg, 3%, 87%);
  /* Chrome, Safari, Edge, Opera */
  /* Firefox */
}
.info__space input:focus {
  outline: hsl(278deg, 68%, 11%) solid 1px;
}
.info__space input::placeholder {
  color: hsl(270deg, 3%, 87%);
  font-family: "Space Grotesk", sans-serif;
  letter-spacing: 1px;
  font-size: 1.5rem;
}
.info__space input::-webkit-outer-spin-button, .info__space input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.info__space input[type=number] {
  -moz-appearance: textfield;
}
.info__date {
  margin-top: 1rem;
  color: hsl(278deg, 68%, 11%);
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  align-items: center;
  gap: 1rem;
  position: relative;
}
.info__date p {
  position: absolute;
  bottom: -1rem;
}
.info__date label {
  font-size: 1.2rem;
}
.info__date input {
  font-family: "Space Grotesk", sans-serif;
  font-size: 1.5rem;
  letter-spacing: 1px;
  display: block;
  width: 100%;
  padding: 1rem;
  border-radius: 1rem;
  border: 1px solid hsl(270deg, 3%, 87%);
}
.info__date input:focus {
  outline: hsl(278deg, 68%, 11%) solid 1px;
}
.info__date input::placeholder {
  color: hsl(270deg, 3%, 87%);
  font-family: "Space Grotesk", sans-serif;
  letter-spacing: 1px;
  font-size: 1.5rem;
}
.info__space-birth {
  grid-column: 1/4;
}
.info__space-year {
  grid-column: 4/6;
}
.info__space-cvc {
  grid-column: 6/-1;
}
.info__btn {
  margin-top: 3rem;
}
.info__btn button {
  cursor: pointer;
  border-radius: 1rem;
  font-family: "Space Grotesk", sans-serif;
  font-size: 2rem;
  width: 100%;
  border: none;
  margin-top: -0.5rem;
  margin-bottom: 5rem;
  background-color: hsl(278deg, 68%, 11%);
  color: hsl(0deg, 0%, 100%);
  padding-block: 2rem;
}
@media (min-width: 1440px) {
  .info__btn button {
    font-size: 1.5rem;
  }
}

.success {
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  text-align: center;
}
@media (min-width: 1440px) {
  .success {
    position: absolute;
    top: 50%;
    width: 25%;
    left: 50%;
    right: 2rem;
    transform: translateY(-80%);
  }
}
.success.none {
  display: none;
}
.success__icon {
  margin-bottom: 1rem;
}
.success__heading {
  margin-bottom: 1.5rem;
}
.success__text {
  color: hsl(279deg, 6%, 55%);
}